import React from 'react'
import '../../App.js'
import '../../App.css'
import { SplitButton, Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';


export default function SignUp() {
    return (
        <div className="sign-up">
            <div class="container">
                <div class="row">
                    <div class="col-md-6 offset-md-3">

                        <form action="" className="Signup">
                            <h3>Sign Up Now!!!</h3>
                            <div class="form-group">
                                <label for="name" className="text-dark">Full Name</label>
                                <input type="text" className="form-control" placeholder="Full Name" name="name" />
                            </div>
                            <div class="form-group ">
                                <label for="email" className="text-dark">Email</label>
                                <input type="text" className="form-control" placeholder="Enter Email" name="email" />
                            </div>


                        </form>
                        <button type="submit" class="btn btn-success text-center">Signup</button>
                    </div>

                </div>
            </div>
        </div>




    )
}